import back from '@/components/back'

export default {
    components:{
        back
    },
    data(){
        return {
			Explain:null,
			name:null,
        }
    },
    created(){
        this.pull()
    },
    methods:{
        pull(){
            this.$post('Prescription/Prescription/GetTCMPrescriptionClassicsInfo',{
                Id:this.$route.query.dId
            }).then(o => {
                this.Explain = o.Explain
				this.name = o.Name
            })
        },
    
    }
}



